<template>
	<div>
		<template v-if="loadingClasses">
			<div class="div__loading">
				<Loader />
			</div>
		</template>
		<template v-else>
			<div class="main__header">
				<div class="main__primary">
					<h3 class="main__title">Search result for {{ query }}</h3>
				</div>
			</div>
			<section class="main-grid__row contacts contacts--list">
				<div class="contacts__wrapper">
					<template v-if="classesLoaded && classes.length">
						<ClassItem 
							v-for="classData in classes"
							:key="classData.id"
							:class-data="classData"/>
					</template>
					<template v-else-if="classesLoaded">
						No Courses created yet
					</template>
					<template v-else>
						Error Loading courses
					</template>
				</div>
			</section>
		</template>
	</div>
</template>

<script>
import { searchCourse } from '../../utils/api/classes';
import Loader from '../../components/Loader';
import ClassItem from './ClassItem';

export default {
	name: 'SearchCourse',
	data() {
		return {
			loading: true,
			query: '',
			classes: [],
			loadingClasses: true,
			classesLoaded: false
		};
	},
	watch: {
		$route() {
			this.searchUser();
		}
	},
	created() {
		this.searchCourse();
	},
	methods: {
		searchCourse() {
			this.query = this.$route.params.query;
			searchCourse(this.query).then((response) => {
				if (response.status === 200) {
					this.classes = response.data.data;
					this.classesLoaded = true;
					return true;
				}
				this.classesLoaded = false;
				return false;
			}).catch(() => {
				this.classesLoaded = false;
			}).finally(() => {
				this.loadingClasses = false;
			});
		}
	},
	components: {
		Loader,
		ClassItem
	}
};
</script>

<style lang="scss" scoped>
.main {
	h3 {
		padding-bottom: 0;
	}
}
</style>
